body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  text-align: center;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.search-form {
  padding: 10px;
  text-align: center;
  background-color:#4e7dd4;
  margin-bottom: 25px;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

img {
  height: 200px;
  width: 270px;
  object-fit: cover;
}

.photo-card {
  align-items: center;
  width: 270px;
  padding: 1px;
  background-color: rgb(231, 230, 230);
  margin-right: 10px;
  margin-bottom: 10px;
}

.info {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  padding: 0 10px 0 10px;
}

.info-item {
  width: auto;
}

.load-more {
  visibility: hidden;
}